.package__check {
    position: absolute;
    width: 32px;
    height: 32px;
    right: 39px;
    top: 34px;
}
.package__check label {
      position: relative;
      display: inline-block;
      width: 32px;
      height: 32px;
      border: 2px solid #cecece;
      border-radius: 18px;
      cursor: pointer;
}
.package__check label:after {
        display: block;
        position: absolute;
        content: '';
        top: 8px;
        right: 8px;
        bottom: 8px;
        left: 8px;
        border-radius: 8px;
        background-color: #acacac;
        -webkit-transition: 0.1s;
        transition: 0.1s;
}
.package__check input {
      position: absolute;
      visibility: hidden;
}
.package__check input:active ~ label:after {
        top: 10px;
        right: 10px;
        bottom: 10px;
        left: 10px;
        background: #2196f3;
}
.package__check input:checked ~ label:after {
        background: #2196f3;
}
